import banner from '../components/banner.vue'
import topBar from '../components/topBar.vue'
export default {
  components: {
    banner: banner,
    topBar: topBar
  },
  data() {
    return {
      swiperList: [],  // 轮播图
      address: '',     // 定位具体位置信息
      params: {
        longitude: '',   // 经度
        latitude: '',    // 纬度
        region: '',      // 省市区字符串逗号隔开的形式
        type: '',        // 店铺类型（如：授权店，专营店，专卖店等）
      },
      storeLists: [],    // 店铺列表
      
    }
  },
  
  onLoad() {
    this.getBanners()
  },
  onShow() {
    let that= this
    if(!that.address) {
      uni.showModal({
        title: '温馨提示',
        content: '请选择你所在的位置信息',
        success: function (res) {
          if (res.confirm) {
            that.getAddress()
          } else if (res.cancel) {
            return
          }
        }
      })
    }
  },
  methods: {
    
    // 获取位置
    getAddress() {
      let that= this
      uni.chooseLocation({
        success: function (res) {
          that.params.region= ''
          that.params.longitude= res.longitude
          that.params.latitude= res.latitude
          that.address= res.address + res.name
          // 获取附近的门店列表
          that.getStoreList()
        }
      })
    },
    // 轮播图
    getBanners() {
      let that= this,
        params= {
          url: that.$api.axhomeBanners
        }
      that.$httpRequest(params).then(res => {
        console.log(res)
        that.swiperList= res.data.list
      })
    },
    
    // 获取门店列表
    getStoreList() {
      let that= this
      if(that.params.region) {
        that.params.longitude= ''
        that.params.latitude= ''
      }
      let params= {
        url: that.$api.axstoreLists,
        data: that.params
      }
      that.$httpRequest(params).then(res => {
        if(res.data.list.length < 1) {
          uni.showToast({
            title: '亲！该地区暂时没有门店哦',
            icon: 'none'
          })
        }
        that.storeLists= res.data.list
      })
    },
    
    // 选择省市区
    bindPickerChange(e) {
      console.log('picker发送选择改变，携带值为', e.target.value)
      this.params.region= e.target.value.join(',')
      
      this.getStoreList()
    },
    
    // 门店详情
    goStoreDetail(item) {
      let params= {
        shop_id: item.shop_id,
        longitude: item.longitude,
        latitude: item.latitude
      }
      uni.navigateTo({
        url: 'storeDetail?data=' + JSON.stringify(params)
      })
    }
  }
}
